@import "../../Style/Color.scss";
@import "../../Style/Template.scss";
.Reload {
    margin-top: 30px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas: ".  RefreshIcon  .";
  .RefreshIcon {
      color: $gold-mustard;
      font-size: 50px;
    text-align: center;
    margin: 0 auto;
    grid-area: RefreshIcon;
  }
}
